html {
  height: 100vh; }

body {
  background-color: $background-color;
  color: $text-color;
  display: flex;
  flex-direction: column;
  font-family: $base-font;
  font-feature-settings: "kern" 1;
  font-kerning: normal;
  font-size: $base-font-size;
  font-weight: $base-font-weight;
  line-height: $base-line-height;
  min-height: 100vh; }

h1, h2, h3, h4, h5, h6 {
  font-family: $header-font;
  letter-spacing: 1px;
  padding-top: $padding / 2;
  @include above($medium-screen) {
    display: flex;
    align-items: center;
    code {
      margin: 0 $padding/4; } } }

h1 {
  font-size: $base-font-size * 1.6;
  @include above($small-screen) {
    font-size: $base-font-size * 3; } }

h2 {
  font-size: $base-font-size * 1.3;
  @include above($small-screen) {
    font-size: $base-font-size * 1.7; } }

h3 {
  font-size: $base-font-size;
  @include above($small-screen) {
    font-size: $base-font-size * 1.1; } }

section {
  h1 {
    padding: 0; } }

p {
  padding: $padding/3 0; }

a, a:visited {
  border-bottom: 2px solid rgba($main-color, 0.30);
  color: $text-color;
  text-decoration: none;
  &:hover {
    border-bottom: 2px solid rgba($main-color, 1);
    color: darken($text-color, 30%);
    transition: border $base-timing ease; } }

strong {
  font-weight: bold; }

ul {
  margin-left: $padding; }

// IMAGES
figure img,
img {
  width: 90%;
  height: auto;
  margin: 0 auto;
  display: block; }

.profile img {
  border-radius: 3px;
  height: 35vw;
  margin: $padding 0;
  object-fit: cover;
  object-position: 0 17%;
  width: 100%; }

figcaption, img + em {
  color: $secondary-text;
  display: block;
  font-size: small;
  text-align: center; }

// DATES
time {
  color: $main-color;
  font-family: $header-font;
  font-size: 75%;
  letter-spacing: 1px;
  text-transform: uppercase;
  @include above($small-screen) {
    font-size: 85%; } }

// TABLES
table {
  border-collapse: collapse;
  margin: $padding auto;
  th {
    border-bottom: 3px double $main-color;
    font-weight: bold;
    text-align: center;
    &:not(:last-child) {
      border-right: $thin-border; } }
  td {
    padding: $padding/6 $padding/2;
    &:not(:last-child) {
      border-right: $thin-border; } } }

// BLOCKQUOTES
blockquote {
  background-color: $lighter-gray;
  border-left: $thick-border;
  padding: 0 $padding;
  margin: $padding 0 $padding $padding * 2;
  code {
    background-color: $light-gray; } }


// CODE FORMATTING
code, pre {
  font-family: monospace;
  background-color: $lighter-gray;
  border-radius: $base-border-radius;
  overflow-x: auto;
  padding: $padding/2;
  margin: $padding/2 0; }

code {
  font-size: $base-font-size * 0.9;
  padding: 2px 6px; }

pre {
  font-size: $base-font-size * 0.9;
  padding: $padding; }

.highlight {
  background: transparent !important; }


